.container {
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: absolute;
}

