body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.view-container {
  top: calc(100vh - 80px);
  height: calc(100vh - 80px);
  width: 100vw;
}

.height-less-nav {
  height: calc(100vh - 80px);
}

.flex-r {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.grid-item {
  width: 100%;
  height: 130px;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, .3);
          box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  margin-bottom: 2px;
}

.grid-item-text {
  font-size: 1rem;
}
.shift-down {
  position: absolute;
  top: 80px;
  width: 100vw;
}
.mobile-nav {
  display: none;
}

@media screen and (max-width: 775px) {
  .navbar {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .navbar a {
    width: 130px;
  }
  .nav-link {
    width: 100%;
  }
}

@media screen and (max-width: 425px) {
  #gmap {
    display: none;
  }
  .list-grid {
    width: 100vw;
  }
}


